body{
  background: #FFBE26;
  word-wrap: break-word;
}
div{
background: blue;
width: 100px;
height: 100px;
margin: 20px 30px 0;
} 
.pierwsza_zakladka{
background: #FFA826;
width: 30%;
min-height: 50px;
height: 100%;
padding: 3px;
border-top-right-radius: 20px;
border-bottom-right-radius: 20px;
}

.druga_zakladka{
background: #FFA826;
width: 40%;
min-height: 50px;
height: 100%;
padding: 3px;
border-top-right-radius: 20px;
border-bottom-right-radius: 20px;
margin: 20px 30px 0;
}
.belka_menu{
background: #FFA826;
width: 1200px;
min-height: 50px;
height: 100%;
padding: 3px;
border-top-right-radius: 20px;
border-top-left-radius: 20px;
border-bottom-right-radius: 20px;
border-bottom-left-radius: 20px;
margin: 20px auto;
}
.glowny{
background: #FFA826;
width: 1000px;
min-height: 500px;
height: 100%;
padding: 3px;
border-top-right-radius: 20px;
border-top-left-radius: 20px;
border-bottom-right-radius: 20px;
border-bottom-left-radius: 20px;
margin: 30px auto;
}
.wyswietlacz{
background: #FF8C26;
width: 300px;
min-height: 400px;
height: 100%;
padding: 3px;
border-top-right-radius: 20px;
border-top-left-radius: 20px;
border-bottom-right-radius: 20px;
border-bottom-left-radius: 20px;
position:relative;
left: 20px;
top: 50px;
border: 2px solid #FFBE26;
}
.uchwyt{
background: #FF8C26;
width: 50px;
min-height: 50px;
height: 100%;
padding: 3px;
border-top-right-radius: 20px;
border-top-left-radius: 20px;
border-bottom-right-radius: 20px;
border-bottom-left-radius: 20px;
position:relative;
right: 50px;
bottom: 225px;
border: 2px solid #000000;
}
.pasek{
background: #FF8C26;
width: 200px;
min-height: 50px;
height: 100%;
padding: 3px;
position:relative;
left: 50%;
bottom: 100px;
border: 2px solid #FFBE26;
}
.news{
display: inline-block;
background: #FFA826;
width: 250px;
min-height: 400px;
height: 100%;
padding: 3px;
border-top-right-radius: 20px;
border-top-left-radius: 20px;
border-bottom-right-radius: 20px;
border-bottom-left-radius: 20px;
margin: 100px 50px 0 ;
}
.wyswietlacz_newsa{
background: #FF8C26;
width: 200px;
min-height: 150px;
height: 100%;
padding: 3px;
border-top-right-radius: 20px;
border-top-left-radius: 20px;
border-bottom-right-radius: 20px;
border-bottom-left-radius: 20px;
position:relative;
right: 10px;
top: 10px;
border: 2px solid #FFBE26;
}


@media (max-width: 500px){
.pierwsza_zakladka
{
max-width:90%;
width:auto;
height:auto;
}
.druga_zakladka
{
max-width:90%;
width:auto;
height:auto;
}
.belka_menu
{
max-width:1200px;
width:auto;
height:auto;
}
.glowny
{
max-width:1000px;
width:auto;
height:auto;
}
.wyswietlacz
{
max-width:300px;
width:auto;
height:auto;
}
.uchwyt
{
max-width:50px;
width:auto;
height:auto;
}
.pasek
{
max-width:200px;
width:auto;
height:auto;
}
.news
{
display: block;
max-width:50px;
width:auto;
height:auto;
}
.wyswietlacz_newsa
{
max-width:50px;
width:auto;
height:auto;
}
}

